;(function () {

    var qrCode = {}

    qrCode.config = {}

    qrCode.install = function (Vue) {
        Vue.directive('qr-code', {
            update (value){
                var self = this
                var target = self.el
                var qrcode = new QRCode(target, {
                    text: value,
                    width: 160,
                    height: 160,
                    colorDark : "#555",
                    colorLight : "#ffffff",
                })
                // qrcode.makeCode(value)
            }
        })
    }
    if (typeof exports == "object") {
        module.exports = qrCode
    } else if (typeof define == "function" && define.amd) {
        define([], function() { return qrCode })
    } else if (window.Vue) {
        window.qrCode = qrCode
        Vue.use(qrCode)
    }
})()



